body{
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    /* color: #707070; */
    /* color: white; */
}

.profile_container{
    margin: auto;
    /* background-color:rgb(54, 54, 54); */
    padding: 5px;
    width: fit-content;
    border-radius: 50%;
}

.profile_container > img {
    border-radius: 50%;
    width: 220px;
}

.name{
    font-size: 33px;
    font-weight: bolder;
    color: whitesmoke;
}

.profession{
    font-size: 19px;
    font-weight: bold;
    color: whitesmoke;
    
}
.skills {
    align-items: center;
}

.skills img{
    width: 80px;
}
.details{
    /* color:grey; */
    color: white;
    font-size: 20px;
}
.decorative_circle{
    bottom: 0px;
    right:-20px;
    /* background-color: rgb(54, 54, 54); */
    height: 50px;
    width: 50px;
    border-radius: 50%;
}
a div{
    /* color: #A8A8A8; */
    color: white;
}

.pattern_down{
    position: absolute;
    top:10;
    left: 0;
    width: 100%;
    z-index: -2;
}